import React, { useState } from 'react';
import { Typography, Form, Input, Button, message, Card, Row, Col, Select } from 'antd';

const { Title, Paragraph } = Typography;
const { Option } = Select;

// 
const initialProjects = [
  {
    id: 1,
    title: 'AI',
    description: '30%',
    category: '',
  },
  {
    id: 2,
    title: '',
    description: 'AIoT200%',
    category: 'AIoT',
  },
  {
    id: 3,
    title: '',
    description: 'AI5000+',
    category: '',
  },
];

function PortfolioEdit() {
  const [projects, setProjects] = useState(initialProjects);
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log(':', values);
    const newProject = { id: projects.length + 1, ...values };
    setProjects([...projects, newProject]);
    message.success('');
    form.resetFields();
  };

  return (
    <div style={{ padding: '50px 0' }}>
      <Title level={2}></Title>
      <Form form={form} onFinish={onFinish} layout="vertical">
        <Form.Item name="title" label="" rules={[{ required: true, message: '' }]}>
          <Input />
        </Form.Item>
        <Form.Item name="description" label="" rules={[{ required: true, message: '' }]}>
          <Input.TextArea rows={4} />
        </Form.Item>
        <Form.Item name="category" label="" rules={[{ required: true, message: '' }]}>
          <Select>
            <Option value=""></Option>
            <Option value="AIoT">AIoT</Option>
            <Option value=""></Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit"></Button>
        </Form.Item>
      </Form>
      <Title level={3}></Title>
      <Row gutter={[16, 16]}>
        {projects.map(project => (
          <Col span={8} key={project.id}>
            <Card title={project.title} bordered={false}>
              <Paragraph>{project.description}</Paragraph>
              <Paragraph>: {project.category}</Paragraph>
              <Button type="link"></Button>
              <Button type="link" danger></Button>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default PortfolioEdit; 